:global{
    .up-gnbl::before {
        content: '.';
        display: block;
        font-size: 0;
        line-height: 0;
        position: absolute;
        width: 16px;
        height: 16px;
        background: url(https://mat1.gtimg.com/news/zhishiguan/page/icon_gnbl.png);
        background-size: 100% 100%;
        left: 20px;
        top: 50%;
        transform: translate(0, -50%);
    }
    .up-gnbl {
        position: relative;
        padding-left: 43px;
        margin-top: 40px;
    }
    .up-tips {
        box-sizing: border-box;
        padding: 0 0 0 45px;
        height: 40px;
        line-height: 40px;
        color: #222222;
        font-size: 16px;
        font-weight: bold;
    }
    .up-tips .tips {
        font-size: 10px;
        color: #737373;
        margin-left: 10px;
        font-weight: normal;
        position: relative;
        top: -2px;
    }
    .up-tips .tip {
        font-size: 13px;
        color: #737373;
        margin-left: 10px;
        font-weight: normal;
        position: relative;
        top: -2px;
    }
    .placeItem.nav {
        background-color: #fff;
        font-size: 12px;
    }
    .placeItem {
        height: 42px;
        line-height: 42px;
        font-size: 12px;
        color: #222;
        text-align: center;
        padding: 0 20px;
        background-color: #f8f8f8;
        position: relative;
        border-bottom: 1px solid #ccc;
    }
    .placeItem.abroad:last-child {
        border: none;
    }
    .placeItem .blue {
        color: #005dff;
    }
    .placeItem > h2 {
        width: 74px;
        height: 100%;
        float: left;
        font-size: 14px;
        text-align: left;
        font-weight: 400;
        overflow: hidden;
    }
    .placeItem > div {
        width: 64px;
        height: 100%;
        font-size: 13px;
        overflow: hidden;
        float: left;
    }
}







